.datatable{
    &_search{
        .form-control{
            padding-left: 50px;
            border: 0;
            background-color: rgba(#000000, 0.06);
        }
        &_icon{
            width: 40px;
            height: 100%;
            top: 0;
            display: flex;
                    align-items: center;
                    justify-content: center;
            &::after{
                position: absolute;
                content: '|';
                color: rgba(#47484a, 0.10);
                right: 0;
            }
            .icon{
                font-size: 18px;
                color: rgba(#47484a, 0.50);
            }
        }
    }
    &_paginate{
        .paginate{
            &_button{
                &:not(:last-child){
                    margin-right: 7px;
                }
                .page-link{
                    width: 32px;
                    height: 32px;
                    border-radius: 50%;
                    border: 0;
                    background-color: rgba(#182a6e, 0.06);
                    color: #182a6e;
                    font-size: 14px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    em{
                        font-size: 12px;
                    }
                    &:hover{
                        background-color: rgba(#182a6e, 0.17);
                    }
                    &:focus{
                        box-shadow: none;
                    }
                }
                &.active{
                    .page-link{
                        background-color: #182a6e;
                        border-color: #182a6e;
                        color: color(50);
                    }
                }
            }
        }
    }
    &-filter{
        .filterData{
            &_icon{
                width: 45px;
                height: 45px;
                background-color: rgba(#182a6e, 0.06);
                border-radius: 4px;
                color: #4B5675;
                display: flex;
                align-items: center;
                justify-content: center;
                .icon{
                    font-size: 24px;
                }
            }
        }
    }
    .table{
        color: #252F4A;
        thead{
            th{
                background: $primary;
                color: $white;
            }
        }
        th, td{
            font-size: 15px;
            padding: 15px;
            color: #252F4A;
            border-right: 1px solid;
            border-color: $white;
        }
        tbody{
            tr{
                th, td{
                    color: #4B5675;
                    transition: all 0.3s ease-in-out;
                    position: relative;
                    &:first-child{
                        &:before{
                            content: "";
                            position: absolute;
                            left: -100%;
                            top: 50%;
                            transform: translateY(-50%);
                            height: 50%;
                            width: 4px;
                            border-top-right-radius: 10px;
                            border-bottom-right-radius: 10px;
                            background-color: #182a6e;
                            transition: left 0.3s ease-in-out;
                        }
                    }
                }
                &:hover{
                    td, th {
                        background: rgba(#182a6e, 0.06);
                        &:first-child{
                            &:before{
                                left: 0;
                            }
                        }
                    }
                }
            }
        }
        &-nowrap{
            th, td{
                white-space: nowrap;
            }
        }
        .w-60{
            width: 60px;
        }
    }
}