.customerPage{
    .cardBox{
        border: 1px solid rgba($primary, 0.20);
        border-radius: 5px;
        box-shadow: 0px 5px 15px rgba($primary,0.10);
        &_left{
            max-width: 130px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            .icon{
                font-size: 60px;
                color: #6495ed;
            }
        }
        &_right{
            border-left: 1px solid rgba($primary, 0.20);
            h2{
                font-size: 30px;
                padding: 15px 20px;
            }
            p{
                font-size: 20px;
                padding: 15px 20px;
                border-bottom: 1px solid rgba($primary, 0.20);
            }
        }
        @include media-breakpoint-down(xxl){
            &_left{
                max-width: 100px;
                .icon{
                    font-size: 40px;
                }
            }
            &_right{
                h2{font-size: 20px;}
                p{font-size: 16px;}
            }
        }
    }
}

//Dashboard Page 
.dashboardData{
    .badge{
        border-radius: 30px;
        padding: 5px 15px;
        margin-right: 5px;
        p{
            font-size: 15px;
            text-transform: uppercase;
            color: #ffffff;
        }
        &-1{
            background-color: #78C169;
        }
        &-2{
            background-color: #D87E77;
        }
        &-3{
            background-color: #BF5FD4;
        }
        &-4{
            background-color: #53A6DF;
        }
        @include media-breakpoint-down(xxl){
            p{
                font-size: 13px; 
            }  
        }
    }
    .userCount{
        padding: 0 15px;
        
        a{
            width: 90px;
            color: $white;
            font-size: 15px;
            text-align: center;
        }
        em{font-size: 24px;margin-right: 15px;}
        &-1{
            em{
                color: #AEC05B;
            }
            a{
                background-color: #AEC05B;
            }
        }
        &-2{
            em{
                color: #9A4DAC;
            }
            a{
                background-color: #9A4DAC;
            }
        }
        &-3{
            em{
                color: #469CF8;
            }
            a{
                background-color: #469CF8;
            }
        }
        &-4{
            em{
                color: #639BE1;
            }
            a{
                background-color: #639BE1;
            }
        }
        &-5{
            p{
                font-size: 20px;
                color: $dark;
            }
            a{
                color: #225399;
                font-size: 15px;
            }
        }
    }
}

.cardRight{
    p{
        font-size: 18px;
    }
    h3{
        font-size: 24px;
    }
}

//Datatable padding common
.table > :not(caption) > * > *{
    padding: 15px;
    font-size: 16px;
}

//locationPage
.locationPage{
    .card{
        box-shadow: none;
    }
}
